header {
	margin: .3rem;
	h1 {
		text-align: center;
		margin-bottom: 0;
	}
	p{
		text-align: center;
		margin-top: 0;
		margin-bottom: 3rem;
	}
}
section#main-info {
	// @include grid($rowz, $colz, $gap) ;
	display: grid;
	grid-template-columns: 30% 60%;	
}
main {
	grid-column-start: 2;

}

#skills {

	ul {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 8px;
		margin-bottom: 2rem;
	}
}
.job-history {
	h3 {
		margin-bottom: 0;
	}
	p {
		margin-top: .2rem;
	}
	li{
		margin: .5rem;
	}
}