// 		MIXINS
@mixin grid($rowsz, $colsz, $gap) {
	display: grid;
	grid-template-rows: repeat(auto-fit, $rowsz);
	grid-template-columns: repeat(20%, 70%);
	grid-gap: $gap;
}



html {
	font-size: 1rem;
	line-height: 1;
}
body {
	box-sizing: border-box;
	
}

header {
	margin: 1rem;
}



